import { Injectable } from '@angular/core';
import {HttpModule, Http, Response, Headers, RequestOptions } from '@angular/http';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { Observable} from 'rxjs/Rx';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/debounceTime';
import 'rxjs/add/operator/distinctUntilChanged';
import 'rxjs/add/operator/switchMap';
import 'rxjs/add/operator/do';
import 'rxjs/add/operator/catch';
import { Customers } from '../Models/customers.class';
import { AssetsDataObject } from '../Models/assets.class';
import { DevicesObject } from '../Models/devices.class';


@Injectable()
export class AuthService {
  public token: string;
  private host =  'http://35.199.99.95:8080';

  private extractData(res: Response) {
    const body = res.json();
    return body.data || [];
  }

  constructor(private http: Http) {

    const currentUser = JSON.parse(localStorage.getItem('currentUser'));
    this.token = currentUser && currentUser.token;
   }

   login(username: string, password: string, loginUrl: string): Observable<boolean> {
     const headers = new Headers();
     headers.append('Content-Type', 'application/json');
     headers.append('Accept', 'application/json');
     return this.http.post(loginUrl,
                      JSON.stringify({ username: username, password: password }),
                      {headers: headers})
                      .map((response: Response) => {
                        const token = response.json() && response.json().token;
                        if (token) {
                          this.token = token;
                          localStorage.setItem('currentUser', JSON.stringify({username: username, token: token}));
                          return true;
                        } else {
                          return false;
                        }
                      });
   }

   getCustomers(token: string, limit: number, textSearch: string): Observable<Customers[]> {
     const customUrl = this.host + '/api/customers?textSearch=' + textSearch + '&limit=' + limit;
     const headers = new Headers();
     headers.append('Content-Type', 'application/json');
     headers.append('X-Authorization' ,  `Bearer ${token}`);

    return this.http.get(customUrl, {headers: headers})
            .map (this.extractData)
            .catch(this.handleError);
   }

  getAssetsbyCustomer(token: string, limit: number, customerId: string): Observable<AssetsDataObject[]> {
     const Url = this.host + '/api/customer/' + customerId + '/assets?limit=' + limit;
     const headers = new Headers();
     headers.append('Content-Type', 'application/json');
     headers.append('X-Authorization' ,  `Bearer ${token}`);
     return this.http.get(Url, {headers: headers})
     .map (this.extractData)
     .catch(this.handleError);
   }

  getDevicesbyCustomer(token: string, limit: number, customerId: string): Observable<DevicesObject[]> {
    const Url = this.host + '/api/customer/' + customerId + '/devices?limit=' + limit;
    const headers = new Headers();
    headers.append('Content-Type', 'application/json');
    headers.append('X-Authorization' ,  `Bearer ${token}`);
    return this.http.get(Url, {headers: headers})
    .map (this.extractData)
    .catch(this.handleError);
  }

  getTelemetry(token: string, deviceId: string, keys: string): Observable<any> {
    const Url = this.host + '/api/plugins/telemetry/DEVICE/' + deviceId + '/values/timeseries?keys=' + keys;
    const headers = new Headers();
    headers.append('Content-Type', 'application/json');
    headers.append('X-Authorization' ,  `Bearer ${token}`);
    return this.http.get(Url, {headers: headers})
    .map ((res: Response) => {
      const body = res.json();
      return body;
    })
    .catch(this.handleError);
  }



   private handleError(error: Response | any) {
       let errMsg: string;
       if (error instanceof Response) {
           const body = error.json() || '';
           const err = body.error || JSON.stringify(body);
           errMsg = `${error.status} - ${error.statusText || ''} ${err}`;
       } else {
           errMsg = error.message ? error.message : error.toString();
       }
       console.error(errMsg);
       return Observable.throw(errMsg);
   }


}



