import {HttpModule, Http, Response, Headers, RequestOptions } from '@angular/http';
import { Observable} from 'rxjs/Rx';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/debounceTime';
import 'rxjs/add/operator/distinctUntilChanged';
import 'rxjs/add/operator/switchMap';
import 'rxjs/add/operator/do';
import 'rxjs/add/operator/catch';
import { Body } from '@angular/http/src/body';

import { AlertService } from './alert.service';




@Injectable()
export class ApiService {
    public token: string;
    public _userName = 'colortel@ucontrol.net.br';
    public _password = 'Colortel@123';
    public _tbBaseUrl = 'http://35.199.99.95:8080';
    public _customerId = '2d977a80-e2a6-11e7-a2cc-0b6a5e10d27e';

  constructor(
    private http: Http,
    private alertService: AlertService

//    private auth: AuthService   --TBD
  ) {
      // set token if saved in local storage
      const currentUser = JSON.parse(localStorage.getItem('currentUser'));
       this.token = currentUser && currentUser.token;

   }


    tbLogin(): Observable<boolean> {
    const headers = new Headers();
    headers.append('Content-Type', 'application/json');
    headers.append('Accept', 'application/json');
    return this.http.post(this._tbBaseUrl + '/api/auth/login',
                            JSON.stringify({ username: this._userName, password: this._password }),
                            {headers: headers})
        .map((response: Response) => {
            // login successful if there's a jwt token in the response
            const token = response.json() && response.json().token;
            if (token) {
                // set token property
                this.token = token;

                // store username and jwt token in local storage to keep user logged in between page refreshes
                localStorage.setItem('currentUser', JSON.stringify({ username: this._userName, token: token }));

                // return true to indicate successful login
                return true;
            } else {
                // return false to indicate failed login
                return false;
            }
        });
    }

    getAssets(tbToken: string): Observable<AssetsDataObject[]> {
        const headers = new Headers();
        headers.append('Content-Type', 'application/json');
        headers.append('X-Authorization' ,  `Bearer ${tbToken}`);
        return this.http.get(this._tbBaseUrl + '/api/customer/' + this._customerId + '/assets?limit=100',
            {headers: headers})
                .map(this.extractData)
                .catch(this.handleError);
    }
    getAssets1(tbToken: string, customerId: string, type: string): Observable<AssetsDataObject[]> {
        const headers = new Headers();
        headers.append('Content-Type', 'application/json');
        headers.append('X-Authorization' ,  `Bearer ${tbToken}`);
        return this.http.get(this._tbBaseUrl + '/api/customer/' + customerId + '/assets?type=' + type + '&limit=200',
            {headers: headers})
                .map(this.extractData)
                .catch(this.handleError);
    }

    getAssetDevices(tbToken: string, _assetId: string): Observable<RelationDataObject[]> {
        const _relationUrl = this._tbBaseUrl + '/api/relations/info?fromId=' + _assetId + '&fromType=ASSET';
        const headers = new Headers();
        headers.append('Content-Type', 'application/json');
        headers.append('X-Authorization' ,  `Bearer ${tbToken}`);
        return this.http.get(_relationUrl,
            {headers: headers})
                .map(res => res.json())
                .catch(this.handleError);
    }

    getTemperature(tbToken: string, _deviceId: string) {
    }

    getHumidity (tbToken: string, _deviceId: string) {

    }

    getPower (tbToken: string, _deviceId: string) {
    }

    getDevices(tbToken: string): Observable<DevicesDataObject[]> {
        const headers = new Headers();
        headers.append('Content-Type', 'application/json');
        headers.append('X-Authorization' ,  `Bearer ${tbToken}`);
        return this.http.get(this._tbBaseUrl + '/api/customer/' + this._customerId + '/devices?limit=200',
            {headers: headers})
                .map(this.extractData)
                .catch(this.handleError);
    }
    getCustomers(tbToken: string): Observable<CustomersDataObject[]> {
        const headers = new Headers();
        headers.append('Content-Type', 'application/json');
        headers.append('X-Authorization' ,  `Bearer ${tbToken}`);
        return this.http.get(this._tbBaseUrl + '/api/customers?limit=200',
        {headers: headers})
        .map(this.extractData)
        .catch(this.handleError);

    }

    getDevicesByCustomer(tbToken: string, customer: string, type: string): Observable<DevicesDataObject[]> {
        const headers = new Headers();
        headers.append('Content-Type', 'application/json');
        headers.append('X-Authorization' ,  `Bearer ${tbToken}`);
        return this.http.get(this._tbBaseUrl + '/api/customer/' + customer + '/devices?type=' + type + '&limit=200',
            {headers: headers})
                .map(this.extractData)
                .catch(this.handleError);
    }

    getDeviceToken(tbToken: string, deviceId: string): Observable<DevicesDataObject[]> {
        const headers = new Headers();
        headers.append('Content-Type', 'application/json');
        headers.append('X-Authorization' ,  `Bearer ${tbToken}`);
        return this.http.get(this._tbBaseUrl + '/api/device/' + deviceId + '/credentials',
            {headers: headers})
                .map(res => res.json())
                .catch(this.handleError);
    }
    getAttributesbyDevice(tbToken: string, deviceToken: string): Observable<AttributesOject[]> {
        const headers = new Headers();
        headers.append('Content-Type', 'application/json');
        headers.append('X-Authorization' ,  `Bearer ${tbToken}`);
        return this.http.get(this._tbBaseUrl + '/api/v1/' + deviceToken + '/attributes',
            {headers: headers})
                .map(res => res.json())
                .catch(this.handleError);
    }

    setAttribute(tbToken: string, deviceId: string, attribute: string, valeu: string): Observable<AttributesOject[]> {
        if (attribute !== undefined && valeu !== undefined && deviceId !== undefined) {
        const body = { [attribute] : valeu};
        console.log('Atributo recebido: ', body);
        const headers = new Headers();
        headers.append('Content-Type', 'application/json');
        headers.append('X-Authorization' ,  `Bearer ${tbToken}`);
        // tslint:disable-next-line:max-line-length
        return this.http.post(this._tbBaseUrl + '/api/plugins/telemetry/DEVICE/' + deviceId + '/SHARED_SCOPE',
        // JSON.stringify(body),
        JSON.stringify({ [attribute] : valeu }),
        {headers: headers})
            // .map(this.extractData)
            .catch(this.handleError);
        } else {
            this.alertService.error('Error');
        }
    }

    logout(): void {
    // clear token remove user from local storage to log user out
    this.token = null;
    localStorage.removeItem('currentUser');
    }

    private extractData(res: Response) {
    const body = res.json();
    return body.data || [];

    }

    private handleError(error: Response|any) {

        let errMsg: string;
        if (error instanceof Response) {
            const body = error.json() || '';
            const err = body.error || JSON.stringify(body);
            this.alertService.error('Error');
            console.log('Error dentro do API Service ', err);
            errMsg = `${error.status} - ${error.statusText || ''} ${err}`;
        } else {
            errMsg = error.message ? error.message : error.toString();
            this.alertService.error('Error');
            console.log('Error dentro do API Service ', errMsg);
        }
        console.error(errMsg);
        return Observable.throw(errMsg);
    }
}



